const FATAL_REBUILD_TOLERANCE = 10
const SETDATA_SCROLL_TO_BOTTOM = {
	scrollTop: 100000,
	scrollWithAnimation: true,
}
export default {
	props: {
		envId: String,
		collection: String,
		groupId: String,
		groupName: String,
		userInfo: Object,
		onGetUserInfo: {
			type: Function,
		},
		getOpenID: {
			type: Function,
		},
	},
	data() {
		return {
			chats: [],
			textInputValue: '',
			openId: '',
			scrollTop: 0,
			scrollToMessage: '',
			hasKeyboard: false,
			db: null,
			scrollWithAnimation: false,
		}
	},
	methods: {
		mergeCommonCriteria(criteria) {
			return {
				groupId: this.groupId,
				...criteria,
			}
		},
		async initRoom() {
			this.try(async () => {
				await this.initOpenID()
				const envId = this.envId
				const collection = this.collection
				const db = this.db = wx.cloud.database({
					env: envId,
				})
				const _ = db.command
				const {
					data: initList
				} = await db.collection(collection).where(this.mergeCommonCriteria()).orderBy('sendTimeTS', 'desc').get()

				console.log('init query chats', initList)

				this.chats = initList.reverse()
				this.scrollTop = 10000,
				this.initWatch(initList.length ? {
					sendTimeTS: _.gt(initList[initList.length - 1].sendTimeTS),
				} : {})
			}, '初始化失败')
		},
		//获取用户openid
		async initOpenID() {
			return this.try(async () => {
				const openId = await this.getOpenID()
				this.openId = openId
			}, '初始化 openId 失败')
		},
		async initWatch(criteria) {
			this.try(() => {
				const collection = this.collection
				const db = this.db
				const _ = db.command
				console.warn(`开始监听`, criteria)
				this.messageListener = db.collection(collection).where(this.mergeCommonCriteria(criteria)).watch({
					onChange: this.onRealtimeMessageSnapshot.bind(this),
					onError: e => {
						if (!this.inited || this.fatalRebuildCount >= FATAL_REBUILD_TOLERANCE) {
							this.showError(this.inited ? '监听错误，已断开' : '初始化监听失败', e, '重连', () => {
								this.initWatch(this.data.chats.length ? {
									sendTimeTS: _.gt(this.data.chats[this.data.chats.length - 1].sendTimeTS),
								} : {})
							})
						} else {
							this.initWatch(this.data.chats.length ? {
								sendTimeTS: _.gt(this.data.chats[this.data.chats.length - 1].sendTimeTS),
							} : {})
						}
					},
				})
			}, '初始化监听失败')
		},
		onRealtimeMessageSnapshot(snapshot) {
			console.warn(`收到消息`, snapshot)

			if (snapshot.type === 'init') {
				this.chats = [
						...this.chats,
						...[...snapshot.docs].sort((x, y) => x.sendTimeTS - y.sendTimeTS),
					]
				this.scrollToBottom()
				this.inited = true
			} else {
				let hasNewMessage = false
				let hasOthersMessage = false
				const chats = [...this.chats]
				for (const docChange of snapshot.docChanges) {
					switch (docChange.queueType) {
						case 'enqueue':
							{
								//判断该条信息是不是自己发的
								hasOthersMessage = docChange.doc._openid !== this.openId
								//查看消息列表中的快照，是否在当前聊天记录中
								const ind = chats.findIndex(chat => chat._id === docChange.doc._id)
								//如果存在
								//如果类型是图片,用快照数据替换占位
								//如果类不是图片,用快照数据替换占位
								if (ind > -1) {
									if (chats[ind].msgType === 'image' && chats[ind].tempFilePath) {
										chats.splice(ind, 1, {
											...docChange.doc,
											tempFilePath: chats[ind].tempFilePath,
										})
									} else chats.splice(ind, 1, docChange.doc)
								} else {
									//如果不存在则判定为新消息,推入群聊记录。
									hasNewMessage = true
									chats.push(docChange.doc)
								}
								break
							}
					}
				}
				this.chats = chats.sort((x, y) => x.sendTimeTS - y.sendTimeTS)
				if (hasOthersMessage || hasNewMessage) {
					this.scrollToBottom()
				}
			}
		},
		//vue中有天然的双向数据绑定，这里需要魔改***********
		//vue中有天然的双向数据绑定，这里需要魔改***********
		//vue中有天然的双向数据绑定，这里需要魔改***********
		async onConfirmSendText() {
			this.try(async () => {
				if (!this.textInputValue) {
					return
				}

				const collection = this.collection
				const db = this.db
				const _ = db.command

				const doc = {
					_id: `${Math.random()}_${Date.now()}`,
					groupId: this.groupId,
					avatar: this.userInfo.avatarUrl,
					nickName: this.userInfo.nickName,
					msgType: 'text',
					textContent: this.textInputValue, //**********************
					sendTime: new Date(),
					sendTimeTS: Date.now(), // fallback
				}

				this.textInputValue = '', //**********************
				this.chats = [
					...this.chats,
					{
						...doc,
						_openid: this.openId,
						writeStatus: 'pending',
					},
				]
				this.scrollToBottom(true)
				await db.collection(collection).add({
					data: doc,
				})
				this.chats = this.chats.map(chat => {
					if (chat._id === doc._id) {
						return {
							...chat,
							writeStatus: 'written',
						}
					} else return chat
				})
			}, '发送文字失败')
		},
		async onChooseImage(e) {
			wx.chooseImage({
				count: 1,
				sourceType: ['album', 'camera'],
				success: async res => {
					const envId = this.envId
						const collection = this.collection
					const doc = {
						_id: `${Math.random()}_${Date.now()}`,
						groupId: this.groupId,
						avatar: this.userInfo.avatarUrl,
						nickName: this.userInfo.nickName,
						msgType: 'image',
						sendTime: new Date(),
						sendTimeTS: Date.now(), // fallback
					}

					this.chats = [
						...this.chats,
						{
							...doc,
							_openid: this.openId,
							tempFilePath: res.tempFilePaths[0],
							writeStatus: 0,
						},
					]
					this.scrollToBottom(true)

					const uploadTask = wx.cloud.uploadFile({
						cloudPath: `${this.openId}/${Math.random()}_${Date.now()}.${res.tempFilePaths[0].match(/\.(\w+)$/)[1]}`,
						filePath: res.tempFilePaths[0],
						config: {
							env: envId,
						},
						success: res => {
							this.try(async () => {
								await this.db.collection(collection).add({
									data: {
										...doc,
										imgFileID: res.fileID,
									},
								})
							}, '发送图片失败')
						},
						fail: e => {
							this.showError('发送图片失败', e)
						},
					})

					uploadTask.onProgressUpdate(({
						progress
					}) => {
						this.chats = this.chats.map(chat => {
							if (chat._id === doc._id) {
								return {
									...chat,
									writeStatus: progress,
								}
							} else return chat
						})
					})
				},
			})
		},
		onMessageImageTap(e) { /****************/
			uni.previewImage({
				urls: [e],
			})
		},
		scrollToBottom(force) {
			if (force) {
				console.log('force scroll to bottom')
				this.scrollTop = 100000
				this.scrollWithAnimation = true
				return
			}
			this.createSelectorQuery().select('.body').boundingClientRect(bodyRect => {
				this.createSelectorQuery().select(`.body`).scrollOffset(scroll => {
					if (scroll.scrollTop + bodyRect.height * 3 > scroll.scrollHeight) {
						console.log('should scroll to bottom')
						this.scrollTop = 100000
						this.scrollWithAnimation = true
					}
				}).exec()
			}).exec()
		},
		async onScrollToUpper() {
			if (this.db && this.chats.length) {
				const collection = this.collection
				const _ = this.db.command
				const {
					data
				} = await this.db.collection(collection).where(this.mergeCommonCriteria({
					sendTimeTS: _.lt(this.chats[0].sendTimeTS),
				})).orderBy('sendTimeTS', 'desc').get()
				this.chats.unshift(...data.reverse())
				this.scrollToMessage = `item-${data.length}`,
				console.log(this.scrollToMessage)
				this.scrollWithAnimation = false
			}
		},
		async try (fn, title) {
			try {
				await fn()
			} catch (e) {
				this.showError(title, e)
			}
		},
		showError(title, content, confirmText, confirmCallback) {
			console.error(title, content)
			uni.showModal({
				title,
				content: content.toString(),
				showCancel: confirmText ? true : false,
				confirmText,
				success: res => {
					res.confirm && confirmCallback()
				},
			})
		}
	},
	created() {
		console.log('hello')
		this.initRoom()
		this.fatalRebuildCount = 0
	}
}
